

import "./index.scss";
import { connect } from "react-redux";
import { getDimission, updatestaffstate, updateliststate, getAsk, updateAskState } from "../../../immutable/actions";
import { Modal, Button, message, Popconfirm } from 'antd';

import { axios } from "&";
const error = () => {
    message.error('该员工已离职，无法进行操作');
}
const warning = () => {
    message.warning('不可再进行重复操作');
};
const text = '你确定真的要撤销申请吗';
const text2 = '你确定要同意申请吗';

@connect(
    state => ({

        dimission: state.getIn(["dimission", 'dimission']),
        ask: state.getIn(["ask", "ask"])

    })
)

export default class socket extends Component {

    constructor() {
        super();
        this.state = {
            visible: false,
            name: ""
        }
    }

    componentDidMount() {
        this.props.dispatch(getDimission({
            url: "/react/searchDimission",
        }))
        this.props.dispatch(getAsk({
            url: "/react/getAsk",
            params: {

            }
        }))

    }

    showModal = (e) => {
        this.setState({
            visible: true,
            name: e.target.parentNode.parentNode.firstElementChild.innerHTML,
        });
        console.log(e.target.parentNode.parentNode.firstElementChild.innerHTML)
    };

    handleOk = e => {
        this.setState({
            visible: false,
        });
        this.props.dispatch(updatestaffstate({
            url: "/react/updatestaffstate",
            params: { name: this.state.name }
        })).then(
            res => {
                this.props.dispatch(updateliststate({
                    url: "/react/updateliststate",
                    params: {
                        name: this.state.name
                    }
                }))
            }
        )
    };

    handleCancel = e => {
        this.setState({
            visible: false,
        });
    };
    getName = (e) => {
        console.log(e.target.parentNode.parentNode.firstElementChild.innerHTML)
        this.setState({
            name: e.target.parentNode.parentNode.firstElementChild.innerHTML,
        })
    }
    confirm = () => {
        this.props.dispatch(updatestaffstate({
            url: "/react/removeDimission",
            params: { name: this.state.name }
        }))
    }
    confirm2 = () => {
        this.props.dispatch(updateAskState({
            url: "/react/updateAskState",
            params: {
                name: this.state.name
            }
        }))
    }


    render() {
        const { dimission, ask } = this.props;
        console.log(this.props)
        return (
            <div className="event">
                <div className="dimission">
                    <h2>离职</h2>
                    <table
                        style={{ wordBreak: "break-all", wordWrap: "break-word" }}
                    >
                        <tbody>
                            <tr>
                                <td>
                                    姓名
                                </td>
                                <td>
                                    入职时间
                                </td>
                                <td>
                                    申请离职时间
                                </td>
                                <td>
                                    离职原因
                                </td>
                                <td>
                                    状态
                                </td>
                                <td>
                                    操作
                                </td>
                            </tr>
                            {
                                dimission.map((item, i) => {
                                    return (
                                        <tr
                                            key={i}
                                            style={{ height: "0.25rem" }}
                                        >
                                            <td>
                                                {item.name}
                                            </td>
                                            <td>
                                                {item.entrytime}
                                            </td>
                                            <td>
                                                {item.dimissiontime}
                                            </td>
                                            <td>
                                                {item.reason}
                                            </td>
                                            <td>
                                                {!item.state ? "在职" : "已离职"}
                                            </td>
                                            <td className="option">
                                                <span
                                                    onClick={!item.state ? this.showModal : error}>
                                                    {!item.state ? "待审批" : "已同意"}
                                                </span>

                                                <Popconfirm
                                                    placement="topLeft"
                                                    title={text}
                                                    onConfirm={!item.state ? this.confirm : error}
                                                    okText="撤销申请"
                                                    cancelText="取消">
                                                    <span onClick={this.getName}>撤销申请</span>
                                                </Popconfirm>
                                            </td>
                                            <Modal
                                                title="离职审批"
                                                visible={this.state.visible}
                                                onOk={this.handleOk}
                                                onCancel={this.handleCancel}
                                            >
                                                你确定要同意该离职申请吗?
                                            </Modal>
                                        </tr>
                                    )
                                })
                            }
                        </tbody>
                    </table>
                </div>
                <div className="ask">
                    <h2>请假</h2>
                    <table>
                        <tbody>
                            <tr>
                                <td>
                                    姓名
                                </td>
                                <td>
                                    开始时间
                                </td>

                                <td>
                                    结束时间
                                </td>
                                <td>
                                    请假原因
                                </td>
                                <td>
                                    操作
                                </td>
                            </tr>
                            {
                                ask.map((item, i) => {

                                    return (
                                        <tr key={i}>
                                            <td>
                                                {item.name}
                                            </td>
                                            <td>
                                                {item.starttime}
                                            </td>

                                            <td>
                                                {item.endtime}
                                            </td>
                                            <td>
                                                {item.reason}
                                            </td>
                                            <td>
                                                <Popconfirm
                                                    placement="topLeft"
                                                    title={text2}
                                                    onConfirm={!item.state ? this.confirm2 : warning}
                                                    okText="同意"
                                                    cancelText="驳回">
                                                    <span onClick={!item.state ? this.getName : warning}>{!item.state ? "待处理" : "已通过"}</span>
                                                </Popconfirm>
                                            </td>
                                        </tr>
                                    )
                                })
                            }
                        </tbody>
                    </table>
                </div>
            </div>
        )
    }
}